@import './../../common/styles/sass/variables.scss';
@import './../../common/styles/sass/mixin';
.project {
    width: 450px;
    display: flex;
    flex-direction: column;
    .image {
        height: 300px;
        display: flex;
        justify-content: center;
        align-items: center;
        background-size:cover ;
        background-position: center;
        background-repeat: no-repeat ;
        position: relative;
        &::before{
            position: absolute;
            content: '';
            background-color: $backGroundColor;
            top: 0;
            bottom: 0;
            left: 0;
            right: 0;
            opacity: 0.4;

        }                     // &- родитель. ::before относится к image
        .viewBtn {
            color: $textColor;
            background-color: $btnColor;
            padding: 10px 20px;
            border-radius: 20px;
            position: relative;
            z-index: 1;
        }
    }

    .projectInfo{
        padding: 25px 20px;
        background-color:$additionalBackGroundColor;
        .projectTitle{
            @include text($titleFontFamily,16px, 1.2);
            letter-spacing: 1px;
            margin: 0 0 5px;

        }
        .description {
            @include text($titleFontFamily,14px, 1.8);
            font-weight: 400;

        }
    }
}








